import React, {useState, useEffect, useRef} from 'react';
import { Row, Col, Button, Container} from 'react-bootstrap';
import { Focusable, FocusableSection } from 'react-js-spatial-navigation';
import Spinner from 'react-bootstrap/Spinner';
import {FaRegStar, FaStar} from 'react-icons/fa';
import { isOpera, CustomView } from 'react-device-detect';
import PES from '../globalVariables';
import './CarePlanMain.scss'
import AddGoal from './AddGoal';
import axios from 'axios';

function MyGoals(props) {
    const urn1 = PES.APIConfig.URN1;
    const urn2 = PES.APIConfig.URN2;
    const MyCarePlanAPI = process.env.NODE_ENV === 'production' ? PES.APIConfig.PROD_API : PES.APIConfig.DEV_API;
  
    const [activityName, setActiveName] = useState<string>('');
    const [myCareGoals, setMyCareGoals] = useState<any>([]);
    const [currentPlan, setPlan] = useState<string>('');
    const [isLoading, setIsLoading] = useState<any>(false);
    const mainMenuItem = useRef<any>([React.createRef(), React.createRef()]);
    const [schedule, setSchedule] = useState<Array<any>>([]);
    const [completedGoals, setCompletedGoals] = useState<any>(false);
    const [goalCount, getGoalCount] = useState<number>(0);
    const [show, setShow] = useState<boolean>(false);
    const globalVersion = "v1";
    let [overrideVersion, usedVersion] = [globalVersion, ""];

    useEffect(() => {
        let cleanup:boolean = false;
        props.setActiveName('MyGoals');
        props.setActiveFocus(3);
        setIsLoading(true);
        usedVersion = overrideVersion;
        axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/${props.id}`)
        .then(response => {
          setMyCareGoals(response.data);
          setIsLoading(false);
          console.log("goals", `${MyCarePlanAPI}/${usedVersion}/careplan/goal/${props.id}`);
          const completedgoals = response.data.filter(function (a) {
            return a.isCompleted;
          });
          if(completedgoals.length !== 0) {
            setCompletedGoals(true);
          }
          getGoalCount(completedgoals.length);
        })
        .catch(error =>{
          console.log(error);
        });
        return ()=>{
            cleanup = true;
        }
    }, []);
    function completeGoal (goal) {
        const params = { isCompleted: true };
        // If override version is not defined, API will use the global version.
        //overrideVersion = "v2";
        usedVersion = overrideVersion;
     
         axios.put(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/update/${goal.fieldId}`, params)
             .then(response => {
               // If override version is not defined, API will use the global version.
               //overrideVersion = "v2";
               usedVersion = overrideVersion;
     
                axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/${urn1}`)
               .then(response => {
                setMyCareGoals(response.data);
                 setIsLoading(false);
                 const completedgoals = response.data.filter(function (a) {
                   return a.isCompleted ;
                 });
                 if(completedgoals.length !== 0) {
                   setCompletedGoals(true);
                 }
                 getGoalCount(completedgoals.length);
               })
               .catch(error =>{
                 console.log(error);
               });
             })
         .catch(error => {
           console.log(error);
         })
       }
     
       function incompleteGoals(goal) {
         const params = { isCompleted: false };
     
         // If override version is not defined, API will use the global version.
         //overrideVersion = "v2";
         usedVersion = overrideVersion;
     
           axios.put(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/update/${goal.fieldId}`, params)
               .then(response => {
                   // If override version is not defined, API will use the global version.
                   //overrideVersion = "v2";
                   usedVersion = overrideVersion;
                   axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/${urn1}`)
                   .then(response => {
                    setMyCareGoals(response.data);
                     setIsLoading(false);
                     const [inCompletedgoals, completedgoals]  = [response.data.filter(function (a) {
                       return !a.isCompleted ;
                     }), response.data.filter(function (a) {
                       return a.isCompleted ;
                     })];
     
                     getGoalCount(Math.abs(inCompletedgoals.length - response.data.length));
     
                     if(completedgoals.length === 0) {
                       setCompletedGoals(false);
                     }
             
                   })
                   .catch(error =>{
                     console.log(error);
                   });
                 })
                 .catch(error => {
                   console.log(error);
                 })
       }

       const handleClose = () => {
         setShow(false); 
         addGoal();
       };
       const handleShow = () => setShow(true); 
     
       const addGoal = () => {
         const title = (document.getElementById("goalTitle") as HTMLInputElement).value;
         const description = (document.getElementById("goalDescription") as HTMLInputElement).value;
     
         const params = {
             Title: title,
             Description: description,
         }
     
         // If override version is not defined, API will use the global version.
         //overrideVersion = "v2";
         usedVersion = overrideVersion;
     
           axios.post(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/create/${props.id}`, params)
               .then(response => {
                   // If override version is not defined, API will use the global version.
                   //overrideVersion = "v2";
                   usedVersion = overrideVersion;
     
                  axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/goal/${props.id}`)
                     .then(response => {
                        setMyCareGoals(response.data);
                       setIsLoading(false);
                       const completedgoals = response.data.filter(function (a) {
                         return a.isCompleted ;
                       });
                       if(completedgoals.length !== 0) {
                         setCompletedGoals(true);
                       }
                       getGoalCount(completedgoals.length);
                     })
                     .catch(error =>{
                       console.log(error);
                     });
              })
              .catch(error => {console.log(error)});
       }
    return (
        <Container fluid={true}>
            {
                isLoading ? 
                (<Spinner className="text-blue" variant="info" animation="border" role="status" >
                </Spinner>): 
                (<>
                    <Row>
                        <Col sm={12}>
                            <Row>
                                {
                                    myCareGoals.length ? (
                                        <>
                                            <Col sm={8} className="text-left pl-5">
                                                <h4 className="goals-count">You have completed {goalCount} of {myCareGoals.length} goals</h4>
                                                <h4 className="goals-motive">Keep going you can do it</h4>
                                            </Col>
                                            <CustomView condition={!isOpera}>
                                                <Col sm={2} className="text-right"><Button onClick={handleShow} className="activity-button-active add-new-goals">Add new Goal</Button></Col>
                                            </CustomView>
                                        </>
                                    ) : null
                                }
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={12} className="pt-3 pl-5 goal-main-content">
                            <FocusableSection  defaultElement={'active'}>
                                <Row className="incomplete-goals">
                                    {
                                        myCareGoals.filter(goal => !goal.isCompleted).map(goal =>
                                            <Col sm={10}  key={goal.fieldId} className="pb-3 goal-card-main mt-2" onClick={()=> completeGoal(goal)}>
                                                <Focusable  className="active" onClickEnter={()=> completeGoal(goal)} >
                                                    <Row className="goals-main">
                                                        <Col sm={12} className="pl-0 pr-0">
                                                            <h3 className="goals-title">
                                                                {goal.header}
                                                            </h3>
                                                            <FaRegStar />
                                                            <p className="goals-description">
                                                                {goal.text}
                                                            </p>
                                                        </Col>
                                                    </Row>
                                                </Focusable>
                                            </Col> 
                                        )
                                    }
                                </Row>
                                <Row>
                                    {
                                    completedGoals ? 
                                        (<h4 className="completed-goals text-left pl-3 mb-3 mt-3">Completed Goals</h4>) : null
                                    }
                                </Row>
                                <Row className="completed-goal">
                                {
                                    myCareGoals.filter(goal => goal.isCompleted).map(goal =>
                                        <Col sm={10}  key={goal.fieldId} className="pb-3 goal-card-main" onClick={()=> incompleteGoals(goal)}>
                                            <Focusable  className="active" onClickEnter={()=> incompleteGoals(goal)} >
                                                <Row className="goals-main">
                                                    <Col sm={12} className="pl-0 pr-0">
                                                        <h3 className="goals-title">
                                                            {goal.header}
                                                        </h3>
                                                        <FaStar />
                                                        <p className="goals-description">
                                                            {goal.text}
                                                        </p>
                                                    </Col>
                                                </Row>
                                            </Focusable>
                                        </Col> 
                                    )
                                }
                                </Row>
                            </FocusableSection>
                        </Col>   
                    </Row>
                 </>   
                
                )
            }
            <AddGoal 
                handleClose={handleClose}
                handleShow={handleShow}
                show={show}
            />
        </Container>
    )
}

export default MyGoals;