import React, {useState, useRef, useEffect, CSSProperties, useReducer} from 'react';
import '../App.css';
import { Container, Row, Col, Spinner} from 'react-bootstrap';
import SpatialNavigation, {FocusableSection } from 'react-js-spatial-navigation';
import Menu from './Menu';
import Header from './Header';
import PES from '../globalVariables';
import './CarePlanMain.scss'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link, useLocation, useParams
} from "react-router-dom";
import Axios from 'axios';

const initialState = {
  patientDetails: {client: {}, bed: {ward: {}}, patient: {}} 
}

const reducer=(state, action)=>{
    switch(action.type) {
      case 'FETCH_SUCCESS':
        return {...state, patientDetails: action.patient}
      case 'FETCH_FAILURE':
        return {patientDetails: {}}
      default:
        return state;
    }
}

const Main: React.FC = () => {
  const myCarebg = PES.PESLiteImages.carePlan;
  const [state, dispatch] = useReducer(reducer, initialState);
  const [myCarePlan, setCarePlan] = useState<Array<any>>([]);
  const [activeFocus, setActiveFocus] = useState<number>(0);
  const [activityName, setActiveName] = useState<string>('');
  const [isLoading, setIsLoading] = useState<any>({carePlanloading: false, activityLoading: false});
  const mainMenuItem = useRef<any>([React.createRef(), React.createRef()]);
  const [homePageLink, setHomePageLink] = useState<boolean>(false);
  const currentMode = PES.APIConfig;
  const API = process.env.NODE_ENV === 'production' ? currentMode.PROD_API : currentMode.DEV_API;
  const globalVersion = "v1";

  useEffect(()=>{
    console.log("link", `${process.env.PUBLIC_URL}`);
    Axios.get(`${API}/${globalVersion}/client/${currentMode.MacID}`)
    .then(res=>dispatch({type:'FETCH_SUCCESS', patient: res.data }))
    .catch(error=>{
      dispatch({type: 'FETCH_FAILURE', patient: {}})
    })
  },[]);

 const MyCarePlan:CSSProperties = {
    backgroundImage: `url(/${myCarebg})`,
  }
  let location = useLocation();
  let background = location.pathname && location.pathname.background;
  return (
    <>
    <div style={MyCarePlan} className={" care-plan-main-body"}>

      { isLoading.carePlanloading ? 
        (<Spinner className="text-blue" variant="warning" animation="border" role="status" >
        </Spinner>) :
        (
          <Container fluid={true}>
          <SpatialNavigation>
          <Row>
          <Col sm={12}>
              <FocusableSection  defaultElement={'active'}>
                <Header activity={activityName}  homePageLink={homePageLink} patient={state.patientDetails.patient}/>
              </FocusableSection>
            </Col>
          </Row>
          <Row >
          <Col sm={12} className="pl-0">
          <Switch location={background || location}>
            <Route path="/:id" children={<Menu  
                mainMenu={myCarePlan} 
                activeFocus={activeFocus}
                mainMenuItem={mainMenuItem}
                setActiveName={setActiveName}
              />} />
          </Switch>
              
            </Col>
          </Row>
          
          </SpatialNavigation>  
        
      </Container>
        )
      }
      
    </div>
    
      </>
  );
}

export default Main;
