import React, {useState, useEffect, useRef} from 'react';
import  { Focusable, FocusableSection } from 'react-js-spatial-navigation';
import {  Button } from 'react-bootstrap';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link, useLocation, useParams
  } from "react-router-dom";
import MyActivity from '../MyCareApp/MyActivity';
import MyGoals from '../MyCareApp/MyGoals';
import PES from '../globalVariables';
import './CarePlanMain.scss'
import AddGoal from './AddGoal';
import axios from 'axios';

function Menu(props) {
    //const urn1 = PES.APIConfig.URN1;
    //const urn2 = PES.APIConfig.URN2;
    const MyCarePlanAPI:any = process.env.NODE_ENV === 'production' ? PES.APIConfig.PROD_API : PES.APIConfig.DEV_API;
  
    const [myCarePlan, setCarePlan] = useState<Array<any>>([]);
    const [activeFocus, setActiveFocus] = useState<number>(0);
    const [myCareSessionActivities, setMyCareSessionActivities] = useState<any>([]);
    const [activityDetails, setActivityDetails] = useState<number>(0);
    const [isLoading, setIsLoading] = useState<any>({carePlanloading: false, activityLoading: false});
    const [schedule, setSchedule] = useState<Array<any>>([]);
    const globalVersion = "v1";
    let [overrideVersion, usedVersion] = [globalVersion, ""];
    let { id } = useParams();
    useEffect(()=> {
        let flag:boolean = false;
      setIsLoading({ carePlanloading: true });
      // If override version is not defined, API will use the global version.
      //overrideVersion = "v2";
      usedVersion = overrideVersion;
        console.log("id", `${process.env.PUBLIC_URL}`);
      axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/section/${id}`)
      .then(response => {
        setCarePlan(response.data); 
        setIsLoading({carePlanloading: false});
      })
      .catch(error =>{
        console.log(error);
      });
      return ()=>{
        flag = true;
      }
    }, []);
  
  
    function toggleActivity(activity) {
      if (activity !== activityDetails) {
        setActivityDetails(activity);
      }
      else {
        setActivityDetails(0);
      }
    }
  
    return (
        <div>
            <Router>
                <div className="d-flex">
                    <div className="sidebar">
                        <FocusableSection  defaultElement={'active'}>
                        {
                            myCarePlan.map((activity, i) =>
                            <Link to={`/mycare/${id}/${activity.code}`} key={i}>
                                <Button className={"activity-button " + (activeFocus === activity.carePlanSectionId ? "activity-button-active " : "activity-button-inactive")}  
                                        >
                                    {activity.header}
                                    <div className={"selection-arrow " + (activeFocus === activity.carePlanSectionId ? "selection-arrow-active "  : "selection-arrow-inactive")}>
                                    </div>
                                </Button>
                            </Link>
                            )
                        }
                    </FocusableSection>
                    </div>
                    <div className="d-flex w-100 justify-content-center p-2">
                    <Switch >
                        <Route path="/mycare/:id/SCHED" children={<MyActivity 
                                showActivityDetials={activityDetails} 
                                toggleActivity={toggleActivity} 
                                schedule={schedule} setActiveFocus={setActiveFocus}
                                setActiveName={props.setActiveName} id={id}
                            />} />
                        <Route path="/mycare/:id/GOALS" children={<MyGoals setActiveFocus={setActiveFocus} setActiveName={props.setActiveName} id={id}/>} />
                    </Switch>
                    </div>
                </div>
            </Router>
        </div>
    )
}

export default Menu
