import React, {useState, useEffect, useRef} from 'react';
import { Row, Col, Card, CardDeck, Container} from 'react-bootstrap';
import { Focusable } from 'react-js-spatial-navigation';
import {FaUser}  from "react-icons/fa";
import Spinner from 'react-bootstrap/Spinner';
import PES from '../globalVariables';
import axios from 'axios';
import {useParams} from 'react-router-dom';

function MyActivity(props) {
    //const urn2 = PES.APIConfig.URN2;
    const MyCarePlanAPI = process.env.NODE_ENV === 'production' ? PES.APIConfig.PROD_API : PES.APIConfig.DEV_API;
    //let { id } = useParams();
    const [activeFocus, setActiveFocus] = useState<number>(0);
    const [activityName, setActiveName] = useState<string>('');
    const [myCareSessionActivities, setMyCareSessionActivities] = useState<any>([]);
    const [activityDetails, setActivityDetails] = useState<number>(0);
    const [isLoading, setIsLoading] = useState<any>({carePlanloading: false, activityLoading: false});
    const [schedule, setSchedule] = useState<Array<any>>([]);
    const globalVersion = "v1";
    let [overrideVersion, usedVersion] = [globalVersion, ""];
  
    useEffect(()=>{
        let isCancelledflg:boolean = false; 
        props.setActiveName('MyActivity');
        props.setActiveFocus(1);
        setIsLoading({activityLoading: true});
          // If override version is not defined, API will use the global version.
          //overrideVersion = "v2";
          usedVersion = overrideVersion;
            //setPlan(item.code);
            axios.get(`${MyCarePlanAPI}/${usedVersion}/careplan/activity/${props.id}`)
            .then(response => {
              setMyCareSessionActivities(response.data);
              setIsLoading({activityLoading: false});
              setSchedule(['Today', 'Tomorrow']);
            })
            .catch(error =>{
              console.log(error);
            });
        return ()=> {
            isCancelledflg = true;
        }
    }, []);
    
      function activateActivity(activity) {
        toggleActivity(activity);
      }
    
      function toggleActivity(activity) {
        if (activity !== activityDetails) {
          setActivityDetails(activity);
        }
        else {
          setActivityDetails(0);
        }
      }
    

    return (
        <Container fluid={true}>
            {
                isLoading.activityLoading ? (<Spinner className=" text-blue" variant="warning" animation="border" role="status" >
                </Spinner>):
                (<>
                    <Col sm={12} className="pt-0 pl-5 care-plan-main-content">
                        <h3 className="care-plan-font-settings text-left pb-1"> {schedule[0]} </h3>
                        <Row className="active">
                            {
                                myCareSessionActivities.map(session =>
                                    session.date !== null ? (
                                        <Col sm={3}  key={session.fieldId} className="pb-2 card-main">
                                            <Focusable  className="active" onClickEnter={()=>toggleActivity(session.fieldId)} onFocus={() => activateActivity(session)}>
                                                <Card  onClick={()=>toggleActivity(session.fieldId)}>
                                                <div className="card-toggle" style={ activityDetails === session.fieldId ? {display: 'block'} : {display: 'none'}}>
                                                        <p> {session.description}</p>
                                                </div>
                                                <Card.Body >
                                                    <Card.Title className="pt-4 ">{session.description}</Card.Title>
                                                    <Card.Text>
                                                    {session.text !== ''? <FaUser/> : <span>&nbsp;</span>} {session.text}
                                                    </Card.Text>
                                                    <Card.Title className="care-plan-time">14:15</Card.Title>
                                                </Card.Body>
                                                <CardDeck><p className="care-plan-status">{session.status}</p></CardDeck>
                                                <Card.Footer style={{backgroundColor: session.statusCSS}}>
                                                </Card.Footer>
                                                </Card>
                                            </Focusable>
                                        </Col>
                                    ): null
                                )
                            }
                        </Row>
                        <h3 className="care-plan-font-settings text-left pb-1"> {schedule[1]} </h3>
                        <Row>
                            {
                                myCareSessionActivities.map(session =>
                                    session.date === null ? (
                                        <Col sm={3}  key={session.fieldId} className="pb-5 card-main">
                                            <Focusable  className="active" onClickEnter={()=>toggleActivity(session.fieldId)} onFocus={() => activateActivity(session)}>
                                                <Card  onClick={()=>toggleActivity(session.fieldId)}>
                                                <div className="card-toggle" style={ activityDetails === session.fieldId ? {display: 'block'} : {display: 'none'}}>
                                                        <p> {session.description}</p>
                                                </div>
                                                <Card.Body >
                                                    <Card.Title className="pt-4 ">{session.description}</Card.Title>
                                                    <Card.Text>
                                                    {session.text !== ''? <FaUser/> : <span>&nbsp;</span>} {session.text}
                                                    </Card.Text>
                                                    <Card.Title className="care-plan-time">10:30 PM</Card.Title>
                                                </Card.Body>
                                                <CardDeck><p className="care-plan-status">{session.status}</p></CardDeck>
                                                <Card.Footer style={{backgroundColor: session.statusCSS}}>
                                                </Card.Footer>
                                                </Card>
                                            </Focusable>
                                        </Col>
                                    ): null
                                )
                            }
                        </Row>
                    </Col>
                </>)
            }     
        </Container>
    )
}

export default MyActivity;
